<template>
  <div class="back-container">
      <div class="header"><h1>棒棒团后台管理系统&nbsp;|&nbsp;页面展示</h1></div>
      <div class="pic-box">
          <ul>
              <li v-for="(item,index) in back" :key="index">
                  <img src="./../assets/loading0.gif" v-view="item.url"/>
                  <h4>{{item.name}}</h4>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
  data() {
      return {
          back: [
              {
                  url: 'http://www.silvermoon.site/test/static/image/back/1.jpg',
                  name: '后台登录界面',
              },
              {
                  url: 'http://www.silvermoon.site/test/static/image/back/2.jpg',
                  name: '后台项目管理界面',
              },
              {
                  url: 'http://www.silvermoon.site/test/static/image/back/3.jpg',
                  name: '系统消息管理界面',
              },
              {
                  url: 'http://www.silvermoon.site/test/static/image/back/4.jpg',
                  name: '后台发送系统通知',
              },
              {
                  url: 'http://www.silvermoon.site/test/static/image/back/5.jpg',
                  name: '编辑及上传项目信息',
              },
          ],
      }
},
}
</script>

<style scoped>
ul,li {
  list-style: none;
}

.pic-box ul {
  margin: 0 100px;
  margin-top: 50px;
}

.pic-box ul li {
  display: inline-block;
  padding: 0 60px;
  margin-bottom: 50px;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.pic-box ul li img{
  width: 400px;
  height: 230px;
  cursor: pointer;
  border: 2px solid white;
  border-radius: 10px;
  box-shadow: 0 0 10px white;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.pic-box ul img:hover {
  -webkit-transform: scale(1.8);
  -moz-transform: scale(1.8);
  transform: scale(1.8);
  -o-transform: scale(1.8);
}
</style>




