<template>
    <div class="back-container">
        <div class="header"><h1>我的简历</h1></div>
        <div class="pic-box">
            <ul>
                <li v-for="(item,index) in resume" :key="index">
                    <img src="./../assets/loading3.gif" v-view="item.url"/>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            resume: [
                {
                    url: 'http://www.silvermoon.site/test/static/image/resume/1.jpg',
                },
                {
                    url: 'http://www.silvermoon.site/test/static/image/resume/2.jpg',
                },
                {
                    url: 'http://www.silvermoon.site/test/static/image/resume/3.jpg',
                },
            ],
        }
},
}
</script>

<style scoped>
ul,li {
    list-style: none;
}

.pic-box ul {
    margin: 0 100px;
    margin-top: 50px;
}

.pic-box ul li {
    display: inline-block;
    padding: 0 60px;
    margin-bottom: 50px;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

.pic-box ul li img{
    width: 750px;
    height: 1110px;
    cursor: pointer;
    border: 2px solid white;
    border-radius: 10px;
    box-shadow: 0 0 10px white;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}
</style>




