import Vue from 'vue'
import Router from 'vue-router'
import Bicycle from '@/views/bicycle'
import Back from '@/views/back'
import Resume from '@/views/resume'
import Fdc from '@/views/fdc'
import Webgl from '@/views/webgl'
import Hw from '@/views/hw'
import Metor from '@/views/metor'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'bicycle',
      component: Bicycle
    },
    {
      path:'/back',
      name: 'back',
      component: Back
    },
    {
      path: '/resume',
      name: 'resume',
      component: Resume
    },
    {
      path: '/fdc',
      name: 'fdc',
      component: Fdc
    },
    {
      path: '/webgl',
      name: 'webgl',
      component: Webgl
    },
    {
      path: '/hw',
      name: 'hw',
      component: Hw
    },
    {
      path: '/metor',
      name: 'metor',
      component: Metor
    }
  ]
})



